<template>
    <div>
        <!-- O componente "nav"  utilizado para criar uma barra de navegao -->
        <nav class="panel">
            <div class="field is-grouped is-grouped-centered">
                <!-- O componente "b-field"  utilizado para criar um campo de formulrio -->
                <b-field align="is-centered" message="Somente nmeros!">
                    <!-- O componente "b-input"  utilizado para criar um campo de entrada de texto -->
                    <b-input v-model="cep" placeholder="CEP aqui..." type="search" icon="magnify"></b-input>
                    <p class="control">
                        <!-- O componente "b-button"  utilizado para criar um boto -->
                        <b-button type="is-primary" :loading="isEspera" label="Buscar" @click="encontrarEndereco"></b-button>
                        <!-- O componente "b-loading"  utilizado para exibir uma animao de carregamento -->
                        <b-loading :is-full-page="true" :active.sync="isEspera"></b-loading>
                    </p>
                </b-field>
            </div>
            <div class="panel">
                <b-field>
                    <!-- O componente "b-dropdown"  utilizado para criar um menu suspenso -->
                    <b-dropdown position="is-bottom-right" :disabled="colunas.length <= 1">
                        <template v-slot:trigger>
                            <b-button>Filtros</b-button>
                        </template>
                        <b-dropdown-item v-for="(coluna, index) in colunas" :key="index">
                            <b-checkbox v-model="coluna.visible">{{ coluna.label }}</b-checkbox>
                        </b-dropdown-item>
                    </b-dropdown>
                </b-field>
                <!-- O componente "b-table"  utilizado para criar uma tabela -->
                <b-table :data="isEmpty ? [] : dados" :columns="colunas">
                    <template #empty>
                        <div class="has-text-centered">No momento no h dados para exibir aqui...</div>
                    </template>
                    <template v-for="coluna in colunas">
                        <b-table-column :key="coluna.cep" v-bind="coluna">
                            <template v-slot="props">
                                {{ props.row[coluna.field] }}
                            </template>
                        </b-table-column>
                    </template>
                </b-table>
            </div>
        </nav>
    </div>
</template>
<script>
import axios from 'axios'

export default {
    data() {
        return {
            cep: '', // Armazena o valor digitado no campo de busca de CEP
            dados: [],
      colunas: [
        { field: 'cep', label: 'CEP', visible: true },
        { field: 'logradouro', label: 'Logradouro', visible: true },
        { field: 'complemento', label: 'Complemento', visible: false },
        { field: 'bairro', label: 'Bairro', visible: true },
        { field: 'localidade', label: 'Localidade', visible: true },
        { field: 'uf', label: 'UF', visible: true },
        { field: 'ibge', label: 'IBGE', visible: true },
        { field: 'gia', label: 'GIA', visible: false },
        { field: 'ddd', label: 'DDD', visible: false },
        { field: 'siafi', label: 'SIAFI', visible: false },
     ],
     isEmpty: false,
     isEspera: false
    }
  },
  mounted() {
    let dadosSalvos = localStorage.getItem('dados')
    if (dadosSalvos) {
        this.dados = JSON.parse(dadosSalvos)
    }
  },
  methods: {
    /**
    Este mtodo  chamado quando o usurio clica no boto "Encontrar Endereo" na aplicao.
    Ele faz uma chamada  API de busca de CEP usando o axios com o valor digitado no campo de busca de CEP (this.cep).
    Antes da chamada  API, a varivel isEspera  definida como true, indicando que a aplicao est esperando uma resposta.
    **/
    async encontrarEndereco() {
     //A varivel isEspera  definida como true, indicando que est esperando uma resposta da aplicao.
     this.isEspera = true;
     /**
     Dentro do bloco try,  feita a chamada  API usando o axios e o valor digitado no campo de busca de CEP.
     Se a resposta retornada pelo servio de busca de CEP contm um erro, uma notificao  exibida ao usurio informando que o CEP no foi encontrado.
     Caso contrrio, os dados retornados so adicionados ao array "dados" e armazenados no localStorage do navegador.
     Em seguida, o campo de busca de CEP  limpo.
     **/
      try {
        // Chamada  API de busca de CEP
        // usando o axios usando o valor digitado no campo de busca de CEP (this.cep)
        const response = await axios.get(`https://viacep.com.br/ws/${this.cep}/json/`)
        if (response.data.erro) {
          this.$buefy.notification.open({
            message: 'CEP no encontrado',
            type: 'is-danger'
          })
        } else {
            this.dados.push({
                ...response.data
            })
            localStorage.setItem('dados', JSON.stringify(this.dados));
            this.cep = ''
        }
        //A varivel isEspera  definida como false, indicando que a aplicao no est mais esperando uma resposta.
        this.isEspera = false


      //Se ocorrer algum erro durante a chamada  API,  exibida uma notificao ao usurio informando que houve um erro ao buscar o CEP.
      } catch (err) {
        //A varivel isEspera  definida como false, indicando que a aplicao no est mais esperando uma resposta.
        this.isEspera = false
        this.$buefy.notification.open({
          message: 'Erro ao buscar CEP',
          type: 'is-danger'
        })
      }
    }
  }
}
</script>